<template lang="pug">
  .exercise-block
    .description Select the answer that best fills the gap.

    .sentence-to-fill
      .chinese(v-html="title")

    .answers
      template(
        v-for="answer, index in answers"
        :v-key="answer.english"
      )
        button.button.answer(
          :class=`{
            'is-answer': isAnswer,
            'selected': isSelected(answer.id),
            'correct': isCorrect(answer.id),
            'wrong': isWrong(answer.id)
          }`
          @click="select(answer.id)"
          :disabled="isAnswer"
        )
          .chinese {{ answer.chn_s }}

          .pinyin {{ answer.pinyin }}
</template>

<script>
import Exercise from '@/mixins/Exercise'

export default {
  name: "QuestionsExercise",
  mixins: [Exercise],
  data() {
    return {
      selected: null
    }
  },
  computed: {
    title() {
      const title = this.exercise.chn_s

      return title.replace(/([_]+)/g, '<span class="empty-space"></span>')
    },
    answers() {
      return this.exercise.answers
    }
  },
  methods: {
    isCorrect(id) {
      if (this.answer !== null) {
        return this.exercise.correct_answer === id
      }

      return false
    },
    isWrong(id) {
      if (this.answer !== null) {
        return this.exercise.correct_answer !== id && this.answer.answer === id
      }

      return false
    },
    isSelected(id) {
      return this.selected === id
    },
    select(id) {
      this.selected = id

      this.emitAnswer()
    },
    emitAnswer() {
      this.$emit('answer', {
        type: 'questions',
        answer: this.selected,
        correct_answer: this.exercise.correct_answer,
        id: this.exercise.id,
        completed: this.selected !== null
      })
    }
  }
}
</script>

