<template lang="pug">
  button.button(@click="updateSettings") Proceed
</template>

<script>
import axios from 'axios';
import { mapState } from 'vuex'

export default {
  name: 'UpdateSettings',
  computed: {
    ...mapState([
      'grade',
    ])
  },
  methods: {
    updateSettings() {
      axios.put('/api/v1/onboarding/level', {
        level: this.grade
      })
        .then(() => {
          window.location.href = '/level?nextPage=true'
        })
        .catch(() => {
          window.location.href = '/level'
        })
    }
  }
}
</script>

<style lang="scss" scoped>
@import "./../assets/scss/button";

.button {
  width: 100%;
  min-height: 3em;

  @media screen and (min-width: 48em) {
    max-width: 100%;
    width: 48%;
    min-height: 3.5em;
  }

  @media screen and (min-width: 64em) {
    max-width: 20.2em;
    width: 100%;
    min-height: 6em;
  }
}
</style>
