<template lang="pug">
  .exercise-block
    .description Select the word that best fits this image

    .image
      img(
        :src="imageUrl(exercise.image)"
      )

    .answers
      template(v-for="answer, index in exercise.answers" :v-key="index")
        button.button.answer(
          :class=`{
            'is-answer': isAnswer,
            'selected': isSelected(answer.id),
            'correct': isCorrect(answer.id),
            'wrong': isWrong(answer.id)
          }`
          @click="select(answer.id)"
          :disabled="isAnswer"
        )
          .chinese {{ answer.chn_s }}

          .pinyin {{ answer.pinyin }}
</template>

<script>
import Exercise from '@/mixins/Exercise'

export default {
  name: 'ImagesExercise',
  mixins: [Exercise],
  data() {
    return {
      selected: null
    }
  },
  methods: {
    imageUrl(image) {
      return require(`@/assets/images/placement_test/${image}`)
    },
    isCorrect(id) {
      if (this.answer !== null) {
        return this.exercise.correct_answer === id
      }

      return false
    },
    isWrong(id) {
      if (this.answer !== null) {
        return this.exercise.correct_answer !== id && this.answer.answer === id
      }

      return false
    },
    isSelected(id) {
      return this.selected === id
    },
    select(id) {
      this.selected = id

      this.emitAnswer()
    },
    emitAnswer() {
      this.$emit('answer', {
        type: 'images',
        answer: this.selected,
        correct_answer: this.exercise.correct_answer,
        id: this.exercise.id,
        completed: this.selected !== null
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../../assets/scss/button";
  @import "../../assets/scss/answers";
  @import "../../assets/scss/exercise";

  .image {
    width: 100%;
    max-height: 300px;
    margin-bottom: 2.5em;

    img {
      display: block;
      margin: 0 auto;
      height: 200px;
      width: auto;

        @media screen and (min-width: 48em) {
            height: 300px;
        }
    }
  }
</style>
