export default {
  props: {
    answer: {
      default: null,
      type: Object,
      required: false
    },
    index: {
      default: 1,
      type: Number,
      required: true
    },
    exercise: {
      default: null,
      type: Object,
      required: true
    }
  },
  computed: {
    isAnswer() {
      return this.answer !== null
    }
  }
}
