import shuffle from "lodash.shuffle"

export default {
  shuffle(test) {
    let newTest = this.shuffleTest(test)

    return this.shuffleTestAnswers(newTest)
  },
  shuffleTest(test) {
    const keys = Object.keys(test)

    for (let key of keys) {
      test[key] = shuffle(test[key])
    }

    return test
  },
  shuffleTestAnswers(test) {
    const keys = Object.keys(test)

    for (let key of keys) {
      for (let i = 0; i < test[key].length; i++) {
        test[key][i].answers = shuffle(test[key][i].answers)
      }
    }

    return test
  }
}
