import Vue from 'vue'
import camelCase from 'lodash.camelcase'
import ImagesExercise from '@/components/exercises/ImagesExercise'
import QuestionsExercise from '@/components/exercises/QuestionsExercise'
import AudioExercise from '@/components/exercises/AudioExercise'
import ComprehensionExercise from '@/components/exercises/ComprehensionExercise'
import ExerciseTypes from '@/helpers/ExerciseTypes'
import store from '@/store'


const imagesExerciseElement = Vue.extend(ImagesExercise)
const questionsExerciseElement = Vue.extend(QuestionsExercise)
const audioExerciseElement = Vue.extend(AudioExercise)
const comprehensionExerciseElement = Vue.extend(ComprehensionExercise)

export default {
  getElement(type, props) {
    const elements = {
      imagesExerciseElement,
      questionsExerciseElement,
      audioExerciseElement,
      comprehensionExerciseElement
    }

    const typeCamelCase = camelCase(type)

    const index = ExerciseTypes.types.findIndex(x => {
      return x.key === type
    })

    if (index !== -1) {
      return new elements[`${typeCamelCase}ExerciseElement`]({
        store,
        propsData: {
          ...props
        }
      })
    }

    throw new Error(
      "Can't create an instance of exercise element. Wrong exercise type!"
    )
  }
}
