<template lang="pug">
  .summary
    h1 Placement Results:

    .result {{ grade }}

    .buttons
      update-settings(@update-settings="updateSettingsEvent(grade)")
      try-again(@try-again="tryAgainEvent")
</template>

<script>
import { mapState } from 'vuex'
import UpdateSettings from "@/components/UpdateSettings"
import TryAgain from "@/components/TryAgain"

export default {
  name: "Summary",
  components: {
    UpdateSettings,
    TryAgain
  },
  computed: {
    ...mapState([
      'grade',
    ])
  },
  methods: {
    updateSettingsEvent(grade) {
      this.$emit('update-settings', grade)
    },
    tryAgainEvent() {
      this.$emit('try-again')
    }
  }
}
</script>

<style lang="scss" scoped>
  .result {
    font-size: 3em;
    text-align: center;
    margin: 2em 0;
  }

  .buttons {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
</style>
