export default {
  types: [
    {
      key: 'images',
      name: 'Images'
    },
    {
      key: 'questions',
      name: 'Questions'
    },
    {
      key: 'audio',
      name: 'Audio'
    },
    {
      key: 'comprehension',
      name: 'Comprehension'
    }
  ],
  getType(count) {
    if (count >= this.types.length) {
      throw new Error()
    }

    return this.types[count].key
  },
  getNextType(type) {
    const currentTypeIndex = this.types.findIndex(x => {
      return x.key === type
    })

    if (currentTypeIndex < this.types.length - 1) {
      return this.types[currentTypeIndex + 1].key
    }

    return ''
  }
}
