import store from '@/store'

import ShufflePlacementTestHelper from '@/helpers/ShufflePlacementTestHelper'
import placementTest from "@/data/placement-test"

import {
  SET_PLACEMENT_TEST_DATA
} from '@/mutation-types'

export default {
  retrievePlacementTest() {
    let test = ShufflePlacementTestHelper.shuffle(placementTest)

    store.commit(SET_PLACEMENT_TEST_DATA, test)
  }
}
