<template lang="pug">
  .exercise-block
    .exercise
      .description Go through the dialogue, then answer the question

      .content
        .text(v-html="addBrs(exercise.text)")
        .question {{ exercise.chn_s }}

      .audio-button-wrapper
        button.button(
          ref="button"
          @click="play"
          :class="{ 'play': icon === 'play', 'pause': icon !== 'play' }"
        )
          font-awesome-icon(ref="icon" :icon="icon" size="lg")

        audio(ref="audio" :src="audioUrl")

    .answers
      template(
        v-for="answer, index in exercise.answers"
        :v-key="answer.english"
      )
        button.button.answer(
          :class=`{
            'is-answer': isAnswer,
            'selected': isSelected(answer.id),
            'correct': isCorrect(answer.id),
            'wrong': isWrong(answer.id)
          }`
          @click="select(answer.id)"
          :disabled="isAnswer"
        )
          .english {{ answer.text }}
</template>

<script>
import Exercise from '@/mixins/Exercise'

export default {
  name: "ComprehensionExercise",
  mixins: [Exercise],
  data() {
    return {
      icon: 'play',
      selected: null
    }
  },
  computed: {
    audioUrl() {
      return this.exercise.audio
    }
  },
  mounted() {
    // remove "stop" class when audio playing has ended
    this.$refs.audio.addEventListener('ended', () => {
      this.toggleIcon()
    })
  },
  methods: {
    addBrs(text) {
      return text.split("\n").join('<br>')
    },
    isCorrect(id) {
      if (this.answer !== null) {
        return this.exercise.correct_answer === id
      }

      return false
    },
    isWrong(id) {
      if (this.answer !== null) {
        return this.exercise.correct_answer !== id && this.answer.answer === id
      }

      return false
    },
    isSelected(id) {
      return this.selected === id
    },
    toggleIcon() {
      this.icon = this.icon === 'play' ? 'pause' : 'play'
    },
    play() {
      const audioElement = this.$refs.audio

      if (audioElement.duration > 0 && !audioElement.paused) {
        audioElement.pause()
        audioElement.load()

        this.toggleIcon()
      } else {
        audioElement.play()

        this.toggleIcon()
      }
    },
    select(id) {
      this.selected = id

      this.emitAnswer()
    },
    emitAnswer() {
      this.$emit('answer', {
        type: 'comprehension',
        answer: this.selected,
        correct_answer: this.exercise.correct_answer,
        id: this.exercise.id,
        completed: this.selected !== null
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../../assets/scss/button";
  @import "../../assets/scss/answers";
  @import "../../assets/scss/exercise";
  @import "../../assets/scss/audio";

  .exercise {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    .content {
      width: 100%;

      font-size: 2em;

      .text {
        display: none;
      }

      .question {
        margin-bottom: 1em;

        font-weight: bold;
        text-align: center;
      }

      @media screen and (min-width: 48em) {
        width: calc(100% - 150px);

        font-size: 1.2em;


        .text {
          display: block;
        }

        .question {
          margin-top: 2em;
          margin-bottom: 2em;
          text-align: left;
        }
      }
    }

      .audio {
          .button {
              @media screen and (min-width: 48em) {
                  font-size: 2em;
              }
          }
      }
  }
</style>
