<template lang="pug">
  button.button(@click="tryAgain") Try again
</template>

<script>
import {
  SET_GRADE,
  SET_PLACEMENT_TEST_DATA,
  SET_CURRENT_EXERCISE,
  SET_CURRENT_EXERCISE_TYPE,
  RESET_ANSWER_HISTORY
} from '@/mutation-types'

import { mapState, mapMutations } from 'vuex'

import ExerciseTypes from '@/helpers/ExerciseTypes'
import ShufflePlacementTestHelper from '@/helpers/ShufflePlacementTestHelper'

export default {
  name: 'TryAgain',
  computed: {
    ...mapState([
      'placementTestData'
    ])
  },
  methods: {
    tryAgain() {
      this.setGrade('Newbie')
      this.setCurrentExercise(0)
      this.setCurrentExerciseType(ExerciseTypes.getType(0))
      this.resetAnswerHistory()


      let test = ShufflePlacementTestHelper.shuffle(this.placementTestData)
      this.setPlacementTestData(test)

      this.$emit('try-again')
    },
    ...mapMutations({
      setGrade: SET_GRADE,
      setPlacementTestData: SET_PLACEMENT_TEST_DATA,
      setCurrentExercise: SET_CURRENT_EXERCISE,
      setCurrentExerciseType: SET_CURRENT_EXERCISE_TYPE,
      resetAnswerHistory: RESET_ANSWER_HISTORY
    })
  }
}
</script>

