<template lang="pug">
  .exercise-view
    keep-alive
      .exercise-container(ref="container")
</template>

<script>
import {
  SET_GRADE,
  SET_CURRENT_EXERCISE,
  SET_CURRENT_EXERCISE_TYPE,
  ADD_TO_ANSWER_HISTORY
} from '@/mutation-types'

import { mapState, mapMutations } from 'vuex'
import ExerciseElementFactory from '@/ExerciseElementFactory'
import ExerciseTypes from '@/helpers/ExerciseTypes'
import camelCase from 'lodash.camelcase'

export default {
  name: 'ExerciseView',
  data() {
    return {
      exercise: {},
      answer: {},
      correctAnswers: 0,
      wrongAnswers: 0
    }
  },
  computed: {
    isAnswer() {
      return Object.keys(this.answer).length > 0 &&
        this.answer.constructor === Object
    },
    isCompleted() {
      return this.isAnswer && this.answer.completed
    },
    placementTest() {
      if (this.placementTestData) {
        return this.placementTestData
      }

      return null
    },
    ...mapState([
      'grade',
      'placementTestData',
      'currentExercise',
      'currentExerciseType'
    ])
  },
  mounted() {
    const type = camelCase(this.currentExerciseType)

    if (this.placementTest !== null) {
      this.exercise = this.placementTest[type][this.currentExercise]
      this.createExerciseElement()
    } else {
      this.$watch('placementTest', () => {
        this.exercise = this.placementTest[type][this.currentExercise]
        this.createExerciseElement()
      })
    }
  },
  methods: {
    createExerciseElement() {
      this.removeOldElements()

      const instance = ExerciseElementFactory.getElement(
        this.currentExerciseType,
        {
          exercise: this.exercise,
          index: this.currentExercise + 1
        }
      )

      instance.$on('answer', this.answerEvent)

      instance.$mount()
      this.$refs.container.appendChild(instance.$el)
    },
    removeOldElements() {
      var myNode = this.$refs.container
      var firstChild = myNode.firstChild

      while(firstChild) {
        myNode.removeChild(firstChild)
        firstChild = myNode.firstChild
      }
    },
    submitAnswer() {
      if (this.isCompleted) {
        this.addToHistory(this.answer)

        if (this.answer.answer === this.answer.correct_answer) {
          this.correctAnswers = this.correctAnswers + 1
        } else {
          this.wrongAnswers = this.wrongAnswers - 1
        }

        this.getNextExercise()

        this.answer = {}
      }
    },
    getNextExercise() {
      const type = camelCase(this.currentExerciseType)

      if (this.currentExercise < this.placementTest[type].length - 1) {
        this.setExerciseForCurrentType(type)
      } else {
        this.levelUp()

        if (this.correctAnswers >= 2) {
          this.correctAnswers = 0
          this.setExerciseForNextType()
        } else {
          this.$emit('finished')
        }
      }
    },
    setExerciseForCurrentType(type) {
      this.setCurrentExercise(this.currentExercise + 1)
      this.exercise = this.placementTest[type][this.currentExercise]

      this.createExerciseElement()
    },
    setExerciseForNextType() {
      const nextType = ExerciseTypes.getNextType(this.currentExerciseType)

      if (nextType !== '') {
        this.setCurrentExercise(0)
        this.setCurrentExerciseType(nextType)

        const nextTypeCamelCase = camelCase(nextType)
        this.exercise = this.placementTest[nextTypeCamelCase][this.currentExercise]
        this.createExerciseElement()
      } else {
        this.$emit('finished')
      }
    },
    answerEvent(answer) {
      this.answer = answer

      this.submitAnswer()
    },
    levelUp() {
      /*
        IMAGES
        [IF Correct Answers >= 2 => Jump Up a Block ELSE Grade as Newbie]

        QUESTIONS
        [IF Correct Answers >= 2 => Jump Up a Block ELSE Grade as Elementary]

        AUDIO
        [IF Correct Answers >= 2 => Jump Up a Level to Intermediate ELSE Grade as Elementary]

        COMPREHENSION
        [IF Correct Answers >= 2 => Jump Up a Level to Upper Intermediate ELSE Grade as Intermediate]
      */

      if (this.currentExerciseType === 'images') {
        if (this.correctAnswers < 2) {
          this.setGrade('Newbie')
        }
      }

      if (this.currentExerciseType === 'questions') {
        if (this.correctAnswers < 2) {
          this.setGrade('Elementary')
        }
      }

      if (this.currentExerciseType === 'audio') {
        if (this.correctAnswers >= 2) {
          this.setGrade('Intermediate')
        } else {
          this.setGrade('Elementary')
        }
      }

      if (this.currentExerciseType === 'comprehension') {
        if (this.correctAnswers >= 2) {
          this.setGrade('Upper Intermediate')
        } else {
          this.setGrade('Intermediate')
        }
      }

      if (this.grade === 'Upper Intermediate' && this.wrongAnswers === 0) {
        this.setGrade('Advanced')
      }
    },
    ...mapMutations({
      setGrade: SET_GRADE,
      setCurrentExercise: SET_CURRENT_EXERCISE,
      setCurrentExerciseType: SET_CURRENT_EXERCISE_TYPE,
      addToHistory: ADD_TO_ANSWER_HISTORY
    })
  }
}
</script>

<style lang="scss" scoped>
  @import "./../assets/scss/button";

  .button-wrapper {
    .button {
      text-transform: uppercase;
      border: 0;

      span {
        margin-right: 0.6em;
      }
    }
  }
</style>
