import Vue from 'vue'
import Vuex from 'vuex'
import ExerciseTypes from '@/helpers/ExerciseTypes'

import {
  SET_GRADE,
  SET_PLACEMENT_TEST_DATA,
  SET_CURRENT_EXERCISE,
  SET_CURRENT_EXERCISE_TYPE,
  ADD_TO_ANSWER_HISTORY,
  RESET_ANSWER_HISTORY
} from '@/mutation-types'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    placementTestData: null,
    grade: 'Newbie',

    currentExercise: 0,
    currentExerciseType: ExerciseTypes.getType(0),

    history: []
  },
  mutations: {
    [SET_GRADE](state, data) {
      state.grade = data
    },
    [SET_PLACEMENT_TEST_DATA](state, data) {
      state.placementTestData = data
    },
    [SET_CURRENT_EXERCISE](state, count) {
      state.currentExercise = count
    },
    [SET_CURRENT_EXERCISE_TYPE](state, type) {
      state.currentExerciseType = type
    },
    [ADD_TO_ANSWER_HISTORY](state, answer) {
      state.history.push(answer)
    },
    [RESET_ANSWER_HISTORY](state) {
      state.history = []
    }
  }
})
